import { html, LitElement } from 'lit';
import { sizeStyles, styles } from './merch-card.css.js';

import {
    ARROW_DOWN,
    ARROW_LEFT,
    ARROW_RIGHT,
    ARROW_UP,
    ENTER,
} from './focus.js';
import './global.css.js';
import {
    EVENT_MERCH_CARD_READY,
    EVENT_MERCH_OFFER_SELECT_READY,
} from './constants.js';

export const MERCH_CARD_NODE_NAME = 'MERCH-CARD';
export const MERCH_CARD = 'merch-card';

const FOOTER_ROW_MIN_HEIGHT = 32; // as per the XD.

const MINI_COMPARE_CHART = 'mini-compare-chart';

export const getRowMinHeightPropertyName = (index) =>
    `--consonant-merch-card-footer-row-${index}-min-height`;

export class MerchCard extends LitElement {
    static properties = {
        name: { type: String },
        variant: { type: String, reflect: true },
        size: { type: String, attribute: 'size', reflect: true },
        badgeColor: { type: String, attribute: 'badge-color' },
        badgeBackgroundColor: {
            type: String,
            attribute: 'badge-background-color',
        },
        badgeText: { type: String, attribute: 'badge-text' },
        icons: { type: Array },
        actionMenu: { type: Boolean, attribute: 'action-menu' },
        actionMenuContent: { type: String, attribute: 'action-menu-content' },
        title: { type: String },
        description: { type: String },
        customHr: { type: Boolean, attribute: 'custom-hr' },
        detailBg: { type: String, attribute: 'detail-bg' },
        secureLabel: { type: String, attribute: 'secure-label' },
        checkboxLabel: { type: String, attribute: 'checkbox-label' },
        selected: { type: Boolean, reflect: true },
        stockOfferOsis: {
            type: Object,
            attribute: 'stock-offer-osis',
            converter: {
                fromAttribute: (value) => {
                    const [PUF, ABM, M2M] = value.split(',');
                    return { PUF, ABM, M2M };
                },
            },
        },
        filters: {
            type: String,
            reflect: true,
            converter: {
                fromAttribute: (value) => {
                    return Object.fromEntries(
                        value.split(',').map((filter) => {
                            const [key, order, size] = filter.split(':');
                            const value = Number(order);
                            return [
                                key,
                                {
                                    order: isNaN(value) ? undefined : value,
                                    size,
                                },
                            ];
                        })
                    );
                },
                toAttribute: (value) => {
                    return Object.entries(value)
                        .map(([key, { order, size }]) =>
                            [key, order, size]
                                .filter((v) => v != undefined)
                                .join(':')
                        )
                        .join(',');
                },
            },
        },
        types: {
            type: String,
            attribute: 'types',
            reflect: true,
        },
    };

    static styles = [styles, ...sizeStyles()];

    customerSegment;
    marketSegment;

    constructor() {
        super();
        this.filters = {};
        this.types = '';
        this.selected = false;
    }

    #container;

    /**
     * @returns {LitElement} Heading M slot
     */
    get miniCompareHeading() {
        return this.querySelector('[slot="heading-m"]');
    }

    /**
     * @returns {LitElement} Body M slot
     */
    get bodySlot() {
        return this.querySelector('[slot="body-m"]');
    }

    /**
     * @returns {LitElement} Price slot
     */
    get priceSlot() {
        return this.querySelector('[slot="heading-m-price"]');
    }

    /**
     * @returns {LitElement} Price Commitment slot
     */
    get commitmentSlot() {
        return this.querySelector('[slot="price-commitment"]');
    }

    /**
     * @returns {LitElement} Offers slot
     */
    get offers() {
        return this.querySelector('[slot="offers"]');
    }

    /**
     * @returns {LitElement} Promo text slot
     */
    get promoText() {
        return this.querySelector('[slot="promo-text"]');
    }

    /**
     * @returns {LitElement} Footer slot
     */
    get footer() {
        return this.querySelector('[slot="footer"]');
    }

    firstUpdated() {
        this.createMiniCompareMobileFilters();
    }

    updated(changedProperties) {
        if (
            changedProperties.has('badgeBackgroundColor') &&
            this.variant !== 'twp'
        ) {
            this.style.border = `1px solid ${this.badgeBackgroundColor}`;
        }
        this.updateComplete.then(async () => {
            this.adjustInnerContent();
            this.adjustFooterRows();
        });
    }

    renderIcons() {
        if (!this.icons || this.icons.length === 0)
            return html`<slot name="icons"></slot>`;
        return html`
            <div class="icons">
                ${this.icons.map((icon) => {
                    let mnemonicLink = this.querySelector(
                        'div[slot="body-xs"]'
                    )?.querySelector('a[href$="#mnemonic-link"]');
                    if (mnemonicLink) {
                        mnemonicLink.href = mnemonicLink.href.replace(
                            '#mnemonic-link',
                            ''
                        );
                    }
                    return mnemonicLink
                        ? html`
                              <a href="${mnemonicLink.href || '#'}">
                                  <img
                                      src="${icon.src}"
                                      alt="${icon.alt}"
                                      loading="lazy"
                                  />
                              </a>
                          `
                        : html`<img
                              src="${icon.src}"
                              alt="${icon.alt}"
                              loading="lazy"
                          />`;
                })}
            </div>
        `;
    }

    get evergreen() {
        return this.classList.contains('intro-pricing');
    }

    get stockCheckbox() {
        return this.checkboxLabel
            ? html`<label id="stock-checkbox">
                    <input type="checkbox" @change=${this.toggleStockOffer}></input>
                    <span></span>
                    ${this.checkboxLabel}
                </label>`
            : '';
    }

    get cardImage() {
        return html` <div class="image">
            <slot name="bg-image"></slot>
            ${this.badge}
        </div>`;
    }

    get secureLabelFooter() {
        const secureLabel = this.secureLabel
            ? html`<span class="secure-transaction-label"
                  >${this.secureLabel}</span
              >`
            : '';
        return html`<footer>${secureLabel}<slot name="footer"></slot></footer>`;
    }

    get badge() {
        let additionalStyles;
        if (!this.badgeBackgroundColor || !this.badgeColor || !this.badgeText) {
            return;
        }
        if (this.evergreen) {
            additionalStyles = `border: 1px solid ${this.badgeBackgroundColor}; border-right: none;`;
        }
        return html`
            <div
                class="${this.variant}-badge"
                style="background-color: ${this.badgeBackgroundColor};
                    color: ${this.badgeColor};
                    ${additionalStyles}"
            >
                ${this.badgeText}
            </div>
        `;
    }

    getContainer() {
        return this.closest('[class*="-merch-cards"]') ?? this.parentElement;
    }

    get headingmMSlot() {
        return this.shadowRoot
            .querySelector('slot[name="heading-m"]')
            .assignedElements()[0];
    }

    get footerSlot() {
        return this.shadowRoot
            .querySelector('slot[name="footer"]')
            ?.assignedElements()[0];
    }

    get price() {
        return this.headingmMSlot?.querySelector('span[is="inline-price"]');
    }

    get checkoutLinks() {
        return [
            ...(this.footerSlot?.querySelectorAll('a[is="checkout-link"]') ??
                []),
        ];
    }

    get isMobileOrTablet() {
        return window.matchMedia('(max-width: 1024px)').matches;
    }

    get isMobile() {
        return window.matchMedia('(max-width: 767px)').matches;
    }

    async toggleStockOffer({ target }) {
        if (!this.stockOfferOsis) return;
        const elements = this.checkoutLinks;
        if (elements.length === 0) return;
        for (const element of elements) {
            await element.onceSettled();
            const planType = element.value?.[0]?.planType;
            if (!planType) return;
            const stockOfferOsi = this.stockOfferOsis[planType];
            if (!stockOfferOsi) return;
            const osis = element.dataset.wcsOsi
                .split(',')
                .filter((osi) => osi !== stockOfferOsi);

            if (target.checked) {
                osis.push(stockOfferOsi);
            }
            element.dataset.wcsOsi = osis.join(',');
        }
    }

    toggleActionMenu(e) {
        const retract = e?.type === 'mouseleave' ? true : undefined;
        const actionMenuContentSlot = this.shadowRoot.querySelector(
            'slot[name="action-menu-content"]'
        );
        if (!actionMenuContentSlot) return;
        actionMenuContentSlot.classList.toggle('hidden', retract);
    }

    handleQuantitySelection(event) {
        const elements = this.checkoutLinks;
        for (const element of elements) {
            element.dataset.quantity = event.detail.option;
        }
    }

    get title() {
        const heading =
            this.variant === 'special-offers'
                ? this.querySelector('[slot="detail-m"]')
                : this.querySelector('[slot="heading-xs"]');
        return heading?.textContent?.trim();
    }

    /**
     * If the card is the single app, set the order for all filters to 2.
     * If not, increment the order for all filters after the second card by 1.
     * @param {*} singleApp
     */
    updateFilters(singleApp) {
        const newFilters = { ...this.filters };
        Object.keys(newFilters).forEach((key) => {
            // if single app set the order for all filters to 2.
            if (singleApp) {
                newFilters[key].order = Math.min(newFilters[key].order || 2, 2);
                return;
            }
            const value = newFilters[key].order;
            // skip first card and cards without explicit order
            if (value === 1 || isNaN(value)) return;
            newFilters[key].order = Number(value) + 1;
        });
        this.filters = newFilters;
    }

    includes(text) {
        return this.textContent.match(new RegExp(text, 'i')) !== null;
    }

    render() {
        if (!this.isConnected || this.style.display === 'none') return;
        switch (this.variant) {
            case 'special-offers':
                return this.renderSpecialOffer();
            case 'segment':
                return this.renderSegment();
            case 'plans':
                return this.renderPlans();
            case 'catalog':
                return this.renderCatalog();
            case 'image':
                return this.renderImage();
            case 'product':
                return this.renderProduct();
            case 'inline-heading':
                return this.renderInlineHeading();
            case MINI_COMPARE_CHART:
                return this.renderMiniCompareChart();
            case 'twp':
                return this.renderTwp();
            default:
                // this line should never hit, to check.
                return this.renderProduct();
        }
    }

    renderSpecialOffer() {
        return html`${this.cardImage}
            <div class="body">
                <slot name="detail-m"></slot>
                <slot name="heading-xs"></slot>
                <slot name="body-xs"></slot>
            </div>
            ${this.evergreen
                ? html`
                      <div
                          class="detail-bg-container"
                          style="background: ${this['detailBg']}"
                      >
                          <slot name="detail-bg"></slot>
                      </div>
                  `
                : html`
                      <hr />
                      ${this.secureLabelFooter}
                  `}`;
    }

    renderSegment() {
        return html` ${this.badge}
            <div class="body">
                <slot name="heading-xs"></slot>
                <slot name="body-xs"></slot>
            </div>
            <hr />
            ${this.secureLabelFooter}`;
    }

    renderPlans() {
        return html` ${this.badge}
            <div class="body">
                ${this.renderIcons()}
                <slot name="heading-xs"></slot>
                <slot name="heading-m"></slot>
                <slot name="body-xxs"></slot>
                <slot name="body-xs"></slot>
                ${this.stockCheckbox}
            </div>
            <slot name="quantity-select"></slot>
            ${this.secureLabelFooter}`;
    }

    renderCatalog() {
        return html` <div class="body">
                <div class="top-section">
                    ${this.renderIcons()} ${this.badge}
                    <div
                        class="action-menu
                        ${this.isMobileOrTablet ? 'always-visible' : ''}
                        ${!this.actionMenu ? 'hidden' : 'invisible'}"
                        @click="${this.toggleActionMenu}"
                    ></div>
                </div>
                <slot
                    name="action-menu-content"
                    class="action-menu-content
                    ${!this.actionMenuContent ? 'hidden' : ''}"
                    >${this.actionMenuContent}</slot
                >
                <slot name="heading-xs"></slot>
                <slot name="heading-m"></slot>
                <slot name="body-xxs"></slot>
                <slot name="body-xs"></slot>
            </div>
            ${this.secureLabelFooter}`;
    }

    renderImage() {
        return html`${this.cardImage}
            <div class="body">
                ${this.renderIcons()}
                <slot name="heading-xs"></slot>
                <slot name="body-xxs"></slot>
                <slot name="body-xs"></slot>
            </div>
            ${this.evergreen
                ? html`
                      <div
                          class="detail-bg-container"
                          style="background: ${this['detailBg']}"
                      >
                          <slot name="detail-bg"></slot>
                      </div>
                  `
                : html`
                      <hr />
                      ${this.secureLabelFooter}
                  `}`;
    }

    renderInlineHeading() {
        return html` ${this.badge}
            <div class="body">
                <div class="top-section">
                    ${this.renderIcons()}
                    <slot name="heading-xs"></slot>
                </div>
                <slot name="body-xs"></slot>
            </div>
            ${!this.customHr ? html`<hr />` : ''} ${this.secureLabelFooter}`;
    }

    renderProduct() {
        return html` ${this.badge}
            <div class="body">
                ${this.renderIcons()}
                <slot name="heading-xs"></slot>
                <slot name="body-xs"></slot>
            </div>
            ${this.secureLabelFooter}`;
    }

    renderMiniCompareChart() {
        // Define the HTML structure for the 'mini-compare-chart' variant here
        return html` <div class="top-section">
                ${this.renderIcons()} ${this.badge}
            </div>
            <slot name="heading-m"></slot>
            <slot name="body-m"></slot>
            <slot name="heading-m-price"></slot>
            <slot name="price-commitment"></slot>
            <slot name="offers"></slot>
            <slot name="promo-text"></slot>
            ${this.secureLabelFooter}
            <slot name="footer-rows"><slot name="body-s"></slot></slot>`;
    }

    renderTwp() {
        return html` ${this.badge}
            <div class="top-section">
                ${this.renderIcons()}
                <slot name="heading-xs"></slot>
                <slot name="body-xs-top"></slot>
            </div>
            <div class="body">
                <slot name="body-xs"></slot>
            </div>
            <footer><slot name="footer"></slot></footer>`;
    }

    connectedCallback() {
        super.connectedCallback();
        this.#container = this.getContainer();
        this.setAttribute('tabindex', '0');
        this.addEventListener('keydown', this.keydownHandler);
        this.addEventListener('mouseleave', this.toggleActionMenu);
        this.addEventListener('change', this.handleQuantitySelection);
        this.addEventListener(
            EVENT_MERCH_OFFER_SELECT_READY,
            this.merchCardReady,
            { once: true }
        );
    }

    disconnectedCallback() {
        super.disconnectedCallback();
        this.removeEventListener('keydown', this.keydownHandler);
        this.removeEventListener('change', this.handleQuantitySelection);
    }

    // custom methods
    keydownHandler(e) {
        const currentCard =
            document.activeElement?.closest(MERCH_CARD_NODE_NAME);
        if (!currentCard) return;
        function selectNextCard(x, y) {
            const el = document
                .elementFromPoint(x, y)
                ?.closest(MERCH_CARD_NODE_NAME);
            if (el) {
                e.preventDefault();
                e.stopImmediatePropagation();
                el.focus();
                el.scrollIntoView({ behavior: 'smooth', block: 'center' });
            }
        }
        const { x, y, width, height } = currentCard.getBoundingClientRect();
        const offset = 64;

        const { code } = e;
        if (code === 'Tab') {
            const focusableElements = Array.from(
                this.querySelectorAll(
                    'a[href], button:not([disabled]), textarea, input[type="text"], input[type="radio"], input[type="checkbox"], select'
                )
            );
            const firstFocusableElement = focusableElements[0];
            const lastFocusableElement = focusableElements[focusableElements.length - 1];

            if (
                !e.shiftKey &&
                document.activeElement === lastFocusableElement
            ) {
                e.preventDefault();
                e.stopImmediatePropagation();
            } else if (
                e.shiftKey &&
                document.activeElement === firstFocusableElement
            ) {
                e.preventDefault();
                e.stopImmediatePropagation();
            }
        } else {
            switch (code) {
                case ARROW_LEFT:
                    selectNextCard(x - offset, y);
                    break;
                case ARROW_RIGHT:
                    selectNextCard(x + width + offset, y);
                    break;
                case ARROW_UP:
                    selectNextCard(x, y - offset);
                    break;
                case ARROW_DOWN:
                    selectNextCard(x, y + height + offset);
                    break;
                case ENTER:
                    if (this.variant === 'twp') return;
                    this.footerSlot?.querySelector('a')?.click();
                    break;
            }
        }
    }

    async adjustInnerContent() {
        if (
            this.variant !== MINI_COMPARE_CHART ||
            this.getBoundingClientRect().width === 0
        )
            return;

        const elements = [
            {
                name: 'miniCompareHeading',
                cssProperty:
                    '--consonant-merch-card-mini-compare-heading-height',
            },
            {
                name: 'bodySlot',
                cssProperty: '--consonant-merch-card-mini-compare-body-height',
            },
            {
                name: 'priceSlot',
                cssProperty: '--consonant-merch-card-mini-compare-price-height',
            },
            {
                name: 'commitmentSlot',
                cssProperty:
                    '--consonant-merch-card-mini-compare-price-commitment-height',
            },
            {
                name: 'offers',
                cssProperty:
                    '--consonant-merch-card-mini-compare-offers-height',
            },
            {
                name: 'promoText',
                cssProperty:
                    '--consonant-merch-card-mini-compare-promo-text-height',
            },
            {
                name: 'footer',
                cssProperty:
                    '--consonant-merch-card-mini-compare-footer-height',
            },
        ];

        const elementMaxHeights = {};

        for (const element of elements) {
            if (this[element.name]) {
                let height =
                    parseInt(
                        window.getComputedStyle(this[element.name]).height
                    ) || 0;
                const cssVarHeight = parseInt(
                        this.#container.style.getPropertyValue(
                            element.cssProperty
                        )
                ) || 0;
                height = Math.max(height, cssVarHeight);
                elementMaxHeights[element.name] = Math.max(
                    elementMaxHeights[element.name] || 0,
                    height
                );
            }
        }

        for (const elementName in elementMaxHeights) {
            const maxHeight = elementMaxHeights[elementName];
            this.#container.style.setProperty(
                elements.find((el) => el.name === elementName).cssProperty,
                `${maxHeight}px`
            );
        }

        for (const element of elements) {
            if (this[element.name]) {
                const cssProperty = element.cssProperty;
                this[element.name].style.height = `var(${cssProperty})`;
            }
        }

        for (const element of elements) {
            const height = parseInt(
                getComputedStyle(this.#container).getPropertyValue(
                    element.cssProperty
                )
            );
            if (height === 0) {
                this[element.name].style.padding = 0;
            }
        }
    }

    adjustFooterRows() {
        if (this.variant !== MINI_COMPARE_CHART) return;
        if (this.getBoundingClientRect().width === 0) return;
        const footerRows = this.querySelector('[slot="footer-rows"]');
        [...footerRows.children].forEach((el, index) => {
            const height = Math.max(
                FOOTER_ROW_MIN_HEIGHT,
                parseInt(window.getComputedStyle(el).height) || 0
            );
            const maxMinHeight =
                parseInt(
                    this.#container.style.getPropertyValue(
                        getRowMinHeightPropertyName(index + 1)
                    )
                ) || 0;
            if (height > maxMinHeight) {
                this.#container.style.setProperty(
                    getRowMinHeightPropertyName(index + 1),
                    `${height}px`
                );
            }
        });
    }

    createMiniCompareMobileFilters() {
        if (this.variant !== MINI_COMPARE_CHART) return;

        const merchCards = Array.from(
            this.#container.querySelectorAll(
                '[class*="-merch-cards"] merch-card'
            )
        );
        if (merchCards.length < 2) return;

        // Hide all merch cards except the first two
        merchCards.slice(2).forEach((card) => (card.style.display = 'none'));

        let mobileFilter = this.#container.querySelector('.mobile-filter');
        if (!mobileFilter) {
            mobileFilter = document.createElement('div');
            mobileFilter.className = 'mobile-filter';
            this.#container.insertBefore(
                mobileFilter,
                this.#container.firstChild
            );

            const leftSelect = document.createElement('select');
            const rightSelect = document.createElement('select');
            mobileFilter.appendChild(leftSelect);
            mobileFilter.appendChild(rightSelect);
        }

        const selectElements = mobileFilter.querySelectorAll('select');
        const leftSelect = selectElements[0];
        const rightSelect = selectElements[1];

        merchCards.forEach((card, index) => {
            const cardTitle = card.querySelector('[slot="heading-m"]');
            const cardTitleValue = cardTitle ? cardTitle.textContent : '';

            const existingOption1 = Array.from(leftSelect.options).find(
                (option) => option.value === cardTitleValue
            );
            const existingOption2 = Array.from(rightSelect.options).find(
                (option) => option.value === cardTitleValue
            );

            if (!existingOption1) {
                const option1 = document.createElement('option');
                option1.value = cardTitleValue;
                option1.text = cardTitleValue;
                leftSelect.appendChild(option1);
            }

            if (!existingOption2) {
                const option2 = document.createElement('option');
                option2.value = cardTitleValue;
                option2.text = cardTitleValue;
                rightSelect.appendChild(option2);

                // Set the default option of the second filter to the title of the second merch card
                if (index === 1) {
                    option2.selected = true;
                }
            }
        });

        // Add event listeners to the select elements to replace the corresponding merch card when an option is selected
        [leftSelect, rightSelect].forEach((select, index) => {
            select.addEventListener('change', () => {
                const selectedOption =
                    select.options[select.selectedIndex].value;
                const allCards = Array.from(
                    this.#container.querySelectorAll(
                        '[class*="-merch-cards"] merch-card'
                    )
                );
                const selectedCard = allCards.find((card) => {
                    const cardTitleText =
                        card.querySelector('[slot="heading-m"]')?.textContent;
                    return cardTitleText.startsWith(selectedOption.trim());
                });

                if (selectedCard) {
                    allCards[index].replaceWith(selectedCard);
                    allCards[index] = selectedCard;
                }
            });
        });
    }

    get merchOfferSelect() {
        return this.querySelector('merch-offer-select');
    }

    merchCardReady() {
        // @ts-ignore
        if (!this.merchOfferSelect.planType) return;
        // add checks for other properties if needed
        this.dispatchEvent(
            new CustomEvent(EVENT_MERCH_CARD_READY, { bubbles: true })
        );
    }
}

customElements.define(MERCH_CARD, MerchCard);
